"use strict";
/**
 * Exports parsed query string parameters. If the same parameter is defined
 * twice, the last value is exported.
 *
 * @exports params
 */

var params = module.exports;

var misc = require("@cosmic-plus/jsutils/es5/misc");

var hiddenKey = misc.setHiddenProperty;

if (location.search) {
  var vars = location.search.substr(1).split("&");
  vars.forEach(function (entry) {
    var field = entry.split("=", 1)[0];
    var value = entry.substr(field.length + 1);
    params[field] = decodeURIComponent(value);
  });
}

hiddenKey(params, "$set", function (obj) {
  Object.keys(obj).forEach(function (key) {
    var value = obj[key];
    if (value == null) delete params[key];else params[key] = value;
  });
  writeQuery();
});

function writeQuery() {
  var query = "";

  for (var field in params) {
    query += "&".concat(field, "=").concat(encodeURIComponent(params[field]));
  }

  query = "?" + query.substr(1);
  if (location.hash) query += location.hash;
  history.replaceState("", null, query);
}